import { BehaviorSubject, Observable, of } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';

// eslint-disable-next-line @typescript-eslint/ban-types
export const cacheItem = <T>(queryMethod: Function) => {
  const subject$ = new BehaviorSubject<T | undefined>(undefined);
  const update = () => queryMethod().pipe(tap((payload: T) => subject$.next(payload)));
  return {
    subject$,
    state$: subject$.pipe(switchMap((payload?: T) => (payload !== undefined ? of(payload) : <Observable<T>>update()))),
    update,
  };
};
